import { ref, onMounted } from "vue";
import jsPDF from "jspdf";
import "jspdf-autotable";
import SignaturePad from "signature_pad";
import Quagga from "quagga";

export default {
  name: "FormularioEntrega",
  setup() {
    const formData = ref({
      data: "",
      nomeRecebedor: "",
      nomeTecnico: "",
    });
    const newRow = ref({
      codigo: "",
      serial: "",
      notaFiscal: "",
    });
    const rows = ref([]);
    let signaturePad = null;

    const addRow = () => {
      if (
        newRow.value.codigo &&
        newRow.value.serial &&
        newRow.value.notaFiscal
      ) {
        rows.value.push({ ...newRow.value });
        newRow.value = { codigo: "", serial: "", notaFiscal: "" };
      }
    };

    const editarLinha = (index) => {
      const row = rows.value[index];
      newRow.value = { ...row };
      rows.value.splice(index, 1);
    };

    const excluirLinha = (index) => {
      rows.value.splice(index, 1);
    };

    const limparAssinatura = () => {
      signaturePad.clear();
    };

    const iniciarLeituraCodigo = (idInput) => {
      Quagga.init(
        {
          inputStream: {
            name: "Live",
            type: "LiveStream",
            target: document.querySelector(`#camera-preview-${idInput}`),
            constraints: {
              width: 320, // Largura desejada (pode ajustar conforme necessário)
              height: 240, // Altura desejada (pode ajustar conforme necessário)
              facingMode: "environment",
            },
          },
          locator: {
            patchSize: "medium",
            halfSample: true,
          },
          numOfWorkers: navigator.hardwareConcurrency,
          decoder: {
            readers: ["code_128_reader", "code_39_reader", "codabar_reader"],
          },
          locate: true,
        },
        (err) => {
          if (err) {
            console.error(err);
            return;
          }
          console.log("Inicialização concluída. Pronto para iniciar");
          Quagga.start();
        }
      );

      Quagga.onDetected((result) => {
        const codigo = result.codeResult.code;
        newRow.value[idInput] = codigo;
        Quagga.stop();
      });
    };

    const gerarPDF = () => {
      const doc = new jsPDF();
      doc.setFontSize(20);
      doc.text("Formulário de Entrega de Peças", 105, 20, { align: "center" });

      doc.setFontSize(12);
      doc.text(`Data: ${formData.value.data}`, 20, 30);
      doc.text(`Nome do Recebedor: ${formData.value.nomeRecebedor}`, 20, 40);
      doc.text(`Nome Técnico: ${formData.value.nomeTecnico}`, 20, 50);

      const tableRows = rows.value.map((row) => [
        row.codigo,
        row.serial,
        row.notaFiscal,
      ]);
      doc.autoTable({
        head: [["Código", "Número de Série", "Nota Fiscal"]],
        body: tableRows,
        startY: 60,
        didDrawCell: (data) => {
          // Adicionar bordas ao redor das células preenchidas
          doc.rect(data.cell.x, data.cell.y, data.cell.width, data.cell.height);
        },
      });

      const signatureImg = signaturePad.toDataURL();
      doc.addImage(
        signatureImg,
        "PNG",
        20,
        doc.autoTable.previous.finalY + 20,
        160,
        50
      );

      doc.save("formulario_entrega.pdf");
    };

    onMounted(() => {
      const canvas = document.getElementById("signatureCanvas");
      signaturePad = new SignaturePad(canvas);
    });

    return {
      formData,
      newRow,
      rows,
      addRow,
      editarLinha,
      excluirLinha,
      limparAssinatura,
      iniciarLeituraCodigo,
      gerarPDF,
    };
  },
};
